.Navigation {
    display: flex;
    position: fixed;
    justify-content: flex-end; 
    top: 0;
    width: 1200px;
    background-color: #777777;
    text-align: right;
  
    &-link {
      color: #f2f2f2;
      text-decoration: none;
      font-weight: bold;
      padding: 15px;
      height: 50px;
      text-transform: uppercase;
    }  
  }
  
 .active {
    background-color: #333333;
  }